@import '../../global.scss';

.about {
	background-color: $mainColor;
	display: flex;
	height: fit-content;
	padding-top: 150px;
	padding-bottom: 150px;

	.wrapper {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: row;
		justify-content: center;
		justify-items: center;
		align-content: center;

		.left {
			width: 65%;
			height: 100%;
			display: flex;
			justify-content: center;
			justify-items: center;
			align-content: center;
			align-items: center;
			text-align: center;

			.content {
				text-align: justify;

				h1 {
					margin-top: 10px;
					font-size: 50px;
					color: rgb(129, 180, 238);
					margin-bottom: 30px;
				}

				p,
				ul,
				h5 {
					font-size: 18px;
					line-height: 40px;
					margin-bottom: 18px;
					color: rgb(143, 202, 250);
				}
			}
		}
	}
}
