.app{
    height: 100vh;
    scrollbar-width: none;  //for firefox
      &::-webkit-scrollbar{
          display: none;
      }
  
    .sections{
        width: 100%;
        height: fit-content;
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        -webkit-overflow-scrolling: touch;
        scrollbar-width: none;  //for firefox
        &::-webkit-scrollbar{
            display: none;
        }
  
        > *{
            width: 100vw;
          //   height: calc(100vh - 70px);
  
            scroll-snap-align: start;
        }
    }
  }